
.app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app-header {
    text-align: center;
    background-color: #35bdb3 !important;
    padding: 12px;
    margin-bottom: 12px;
}
.app-header h1 {
      color: #fff;
}
.app-header h1 i {
        font-size: 1.3em;
}
.app-header i {
      color: #fff;
      cursor: pointer;
}
.app-header .dropdown span {
      color: #fff;
}
.app-content {
    margin-top: 5em;
    margin-bottom: 5em;
}
.app-footer {
    text-align: center;
    background-color: #35bdb3 !important;
    color: #fff;
    border-radius: 0px !important;
    position: fixed !important;
    bottom: 0;
    width: 100%;
    z-index: 99;
}

.todo-list {
  padding: 0% 5%;
}
.todo-list__header {
    margin-bottom: 12px;
}
.todo-list__header h3, .todo-list__header h6 {
      margin: 0px;
}
.todo-list__header .list-summary__label {
      margin-bottom: 9px;
}

.todo-item__content .item-action {
  cursor: pointer;
  font-size: 1.5em;
}
.todo-item__content .header {
  text-decoration: underline;
  font-size: 1.3em !important;
}
.todo-item__content .description {
  text-align: justify;
  text-indent: 1.8em;
}
.todo-item__content .info {
  margin-top: 9px;
  padding-top: 6px;
  font-size: 0.8em;
  border-top: 1px dashed #f0f0f0;
}
.todo-item__content .info-meta__user {
    font-weight: 500;
}
.todo-item__content .info-meta__date {
    font-style: italic;
}
.todo-item__actions {
  text-align: center;
}

.todo-detail {
  padding: 0% 5%;
}
.todo-detail header {
    border-bottom: 1px solid #f0f0f0;
    margin-bottom: 12px;
    padding-bottom: 6px;
}
.todo-detail__info {
    margin: 1.5em 0em;
}
.todo-detail__info .info-meta {
      font-size: 0.8em;
}
.todo-detail__info .info-meta p {
        text-indent: 0;
        margin-bottom: 3px;
}
.todo-detail__info .info-meta__user {
        font-weight: 500;
}
.todo-detail__info .info-meta__date {
        font-style: italic;
}
.todo-detail__info h4 {
      text-decoration: underline;
      margin-top: 1em;
      font-size: 1.3em !important;
}
.todo-detail__info p {
      text-align: justify;
      text-indent: 1.5em;
}
.todo-detail__comments .comments-list {
    height: 200px;
    overflow: scroll;
}
.todo-detail__comments .comments-item__other {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin-right: 10%;
    float: left;
    width: 100%;
    margin-bottom: 15px;
}
.todo-detail__comments .comments-item__other .item-avatar {
      width: 15%;
      text-align: center;
      padding-top: 18px;
}
.todo-detail__comments .comments-item__other .item-content {
      margin-right: 40%;
      width: 100%;
}
.todo-detail__comments .comments-item__other .item-content .author {
        display: block;
}
.todo-detail__comments .comments-item__other .item-content .text {
        text-align: left;
        font-weight: 300;
        padding: 5px 10px;
        border-radius: 2px 10px 10px 2px;
}
.todo-detail__comments .comments-item__other .item-content .timestamp {
        font-size: 0.8em;
        color: grey;
}
.todo-detail__comments .comments-item__own {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin-right: 4%;
    float: right;
    width: 100%;
    margin-bottom: 15px;
}
.todo-detail__comments .comments-item__own .item-content {
      margin-left: 40%;
      width: 100%;
      text-align: right;
}
.todo-detail__comments .comments-item__own .item-content .text {
        text-align: right;
        font-weight: 300;
        padding: 5px 10px;
        border-radius: 10px 2px 2px 10px;
}
.todo-detail__comments .comments-item__own .item-content .timestamp {
        font-size: 0.8em;
        color: grey;
}
.todo-detail__comments .comments-locked {
    font-size: 0.5em;
    text-transform: normal;
}
.todo-detail__comments form {
    border-top: 1px solid #f0f0f0;
    padding-top: 1em;
}
.todo-detail__comments form textarea {
      height: 100% !important;
}
.todo-detail__comments form .message-error {
      color: red;
      margin-bottom: 6px;
}

.todo-form {
  padding: 0% 5%;
}
.todo-form header {
    border-bottom: 1px solid #f0f0f0;
    margin-bottom: 12px;
    padding-bottom: 6px;
}

.form .message-error {
  color: red;
  margin-bottom: 6px;
}

.auth {
  padding: 0% 5%;
  margin: 0 auto;
  max-width: 400px;
}
.auth-header {
    text-align: center;
    margin-bottom: 12px;
}
.auth-footer {
    margin-top: 30px;
    border-top: 1px solid #f0f0f0;
    padding-top: 12px;
}
.auth-footer a {
      text-decoration: underline;
      cursor: pointer;
}
